@import url(https://fonts.googleapis.com/css?family=Sansita|Noto+Serif|Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg {
    position: absolute;
    width: 50vw;
    height: 100vh;
    top: 0;
    z-index: -999;
    transition: all 300ms ease-in-out;
    -webkit-transition: all 300ms ease-in-out;
    -moz-transition: all 300ms ease-in-out;
    -ms-transition: all 300ms ease-in-out;
    -o-transition: all 300ms ease-in-out;
}

.left {
    background-color: #373061;
    left: 0;
    overflow: auto;
}

.right {
    background-color: #3A5864;
    right: 0;
}

.bg-move {
    width: 0vw;
}

@media only screen and (max-width: 736px),
screen and (max-height: 500px) {
    .bg {
        width: 100vw;
        height: 50vh;
        top: auto;
    }

    .left {
        top: 0;
        left: 0;
    }

    .right {
        bottom: 0;
        left: 0;
    }

    .bg-move {
        height: 0vh;
    }
}
.app-header {
    display: flex;
    justify-content: space-between;
    height: 23vh;
}

.header-icons {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0.5%;
}

.header-icon-container {
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    color: #373061;
    opacity: 1;
    transition: all 300ms ease-in-out;
    -webkit-transition: all 300ms ease-in-out;
    -moz-transition: all 300ms ease-in-out;
    -ms-transition: all 300ms ease-in-out;
    -o-transition: all 300ms ease-in-out;
}

.header-icon-container>.icon-detail {
    opacity: 0;
    display: none;
    transition: all 300ms ease-in-out;
    -webkit-transition: all 300ms ease-in-out;
    -moz-transition: all 300ms ease-in-out;
    -ms-transition: all 300ms ease-in-out;
    -o-transition: all 300ms ease-in-out;
}

.header-icon-container:hover {
    cursor: pointer;
    color: #fff;
    width: 150px;
}

.header-icon-container:hover>.header-icon {
    opacity: 0;
    display: none;
}

.header-icon-container:hover>.icon-detail {
    opacity: 1;
    display: flex;
}

.icon-detail {
    font-size: 14px;
    font-weight: bold;
    font-family: 'Sansation', sans-serif;
}

.facebook:hover {
    background-color: #3b5998;
}

.instagram:hover {
    background-color: #c32aa3;
}

.twitter:hover {
    background-color: #1da1f2;
}

.github:hover {
    background-color: #333;
}

.header-title {
    align-self: center;
    font-size: 40px;
    color: rgba(255, 255, 255, 0.36);
    font-weight: bold;
    font-family: 'Sansation', sans-serif;
}

.about-button-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 1%;
}

.about-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90px;
    height: 35px;
    background-color: #04AFF8;
    color: #fff;
    font: 'Sansation', sans-serif;
    font-weight: bold;
    font-size: 16px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    transition: all 200ms ease-in-out;
    -webkit-transition: all 200ms ease-in-out;
    -moz-transition: all 200ms ease-in-out;
    -ms-transition: all 200ms ease-in-out;
    -o-transition: all 200ms ease-in-out;
}

.about-button:hover {
    opacity: 0.7;
    cursor: pointer;
}

@media only screen and (max-width: 736px),
screen and (max-height: 500px) {
    .about-button {
        width: 50px;
        height: 20px;
        font-size: 10px;
    }

    .about-button-container {
        margin-right: 5px;
        margin-top: 10px;
        padding: 0;
    }

    .header-title {
        font-size: 16px;
    }

    .icon-detail {
        font-size: 9px;
    }

    .header-icon-container:hover {
        width: 90px;
    }

    .header-icons {
        justify-content: space-around;
        padding: 0;
        margin-left: 5px;
    }
}
.home-body {
    display: flex;
    flex-direction: column;
    height: 60vh;
    justify-content: space-around;
    align-items: center;
    color: #fff;
}

.home-body .question {
    font-family: 'Sansation', sans-serif;
    font-weight: bold;
    font-size: 18px;
    flex: 1 1;
}

.home-options-container {
    display: flex;
    flex-direction: column;
    flex: 3 1;
    justify-content: space-between;
    align-items: center;
}

.home-options-container .home-option {
    font-family: 'Noto Serif', serif;
    font-weight: bold;
    font-size: 85px;
    transition: all 300ms ease-in-out;
    -webkit-transition: all 300ms ease-in-out;
    -moz-transition: all 300ms ease-in-out;
    -ms-transition: all 300ms ease-in-out;
    -o-transition: all 300ms ease-in-out;
    opacity: 1;
    color: #fff;
}

.home-options-container:hover .home-option {
    font-size: 80px;
    opacity: 0.2;
}

.home-options-container .home-option:hover {
    cursor: pointer;
    font-size: 100px;
    opacity: 1;
}

.artist:hover {
    color: #04AFF8;
}

.programmer:hover {
    color: #FE3131;
}

@media only screen and (max-width: 736px),
screen and (max-height: 500px) {
    .home-body .question {
        font-size: 10px;
    }

    .home-options-container .home-option {
        font-size: 40px;
    }

    .home-options-container:hover .home-option {
        font-size: 35px;
    }

    .home-options-container .home-option:hover {
        font-size: 45px;
    }

    .home-options-container {
        flex: 2 1;
        justify-content: space-between;
        margin-bottom: 40px;
    }
}
.app-footer {
    display: flex;
    justify-content: space-between;
    height: 17vh;
    align-items: center;
    color: #fff;
    width: 100%;
}

.envelope {
    margin-right: 3%;
    display: flex;
    background-color: #04AFF8;
    height: 50px;
    width: 50px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: all 300ms ease-in-out;
    -webkit-transition: all 300ms ease-in-out;
    -moz-transition: all 300ms ease-in-out;
    -ms-transition: all 300ms ease-in-out;
    -o-transition: all 300ms ease-in-out;
}

.copyright {
    margin-left: 3%;
    display: flex;
    align-items: center;
}

.envelope:hover {
    cursor: pointer;
    opacity: 0.7;
}
.bg-image {
    position: absolute;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: -1000;
}
